import React from 'react'
import styles from './FormsControls.module.css'
import { Field } from 'redux-form';
import { Checkbox } from '@material-ui/core';

const FormsControl = ({ input, meta: {touched, error}, child, ...props }) => {
   const hasError = touched && error

      return (
      <div className={hasError ? styles.error : null}>
         <div>
            {props.children}
         </div>
         {hasError && <span>{error}</span>}
      </div>
      );
 }

export const Textarea = (props) => {
   const { input, meta, child, ...restProps } = props;
   return <FormsControl {...props} > {<textarea {...input} {...restProps} />} </FormsControl>;
}

export const Input = (props) => {
   const { input, meta, child, ...restProps } = props;
  return (
    <FormsControl {...props} > {<input {...input} {...restProps} />} </FormsControl>
  )
}

export const CheckboxForm = (formData) => {
   return <Checkbox
        checked={formData.rememberMe}
        onChange={formData.rememberMe}
        color="secondary"
        inputProps={{ 'aria-label': 'primary checkbox' }}
      />
}

export const createField = (placeholder, name, components, validators, props = {} ) => {
         return (
           <Field
             placeholder={placeholder}
             name={name}
             component={components}
             validate={validators}
             {...props}
           /> 
         );
       };