import React from 'react';
import { reduxForm } from 'redux-form';
import { Input, createField, CheckboxForm } from "../common/FormsControls/FormsControls";
import { required } from '../../validators';
import { connect } from 'react-redux';
import { login, logOut } from './../../redux/auth-reducer'
import { Redirect } from 'react-router-dom';
import styles from './../../components/common/FormsControls/FormsControls.module.css';
import { Button } from '@material-ui/core';
import classes from './Login.module.css'


const LoginForm = ({ handleSubmit, error }) => {

  return <>
      <form className={classes.form} onSubmit={handleSubmit}>
        <div>{createField("email", "email", Input, [required])}</div>
        <div>
          {createField("Password", "password", Input, [required], {
            type: "password",
          })}
        </div>
        <div>
          {createField(null, "rememberMe", CheckboxForm, [] )}
          remember me
        </div>
        {error ? <div className={styles.formSummaryError}>
            {error}
          </div> : null}
        <div className={classes.btn}>
          <Button size="small" variant="contained" color="primary" type="submit">
            Login
          </Button>
        </div>
      </form>
    </>;
};

const LoginReduxForm = reduxForm({
  form: "login"
})(LoginForm);


const Login = (props) => {
  const onSubmit = formData => {
    props.login(formData.email, formData.password, formData.rememberMe);
  };

if (props.isAuth) {
  return <Redirect to={"/profile"} />
}

  return (
    <div>
      <h1>Login</h1>
      <LoginReduxForm onSubmit={onSubmit} />
    </div>
  );
};

const mapStateToProps = (state) => ({
  isAuth: state.auth.isAuth,
})

export default connect(mapStateToProps, { login, logOut })(Login);