import React from 'react';
import classes from './Header.module.css';
import { NavLink } from 'react-router-dom';
import { Button, IconButton } from '@material-ui/core';
import AccountCircle from "@material-ui/icons/AccountCircle";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";

const Header = (props) => {
    return <header className={classes.header}>
        <img src="https://cdn.worldvectorlogo.com/logos/react-academy.svg" alt="pic" />

        <div className={classes.loginBlock}>
          {props.isAuth ? <div className={classes.btn}>
              <span className={classes.text}>
                {props.login}
                <IconButton color="inherit">
                  <AccountCircle />
                </IconButton>

                <IconButton color="inherit">
                  <ExitToAppIcon onClick={props.logOut} />
                </IconButton>
              </span>
              {/* <Button size="small" color="secondary" onClick={props.logOut}>
                Logout
              </Button> */}
            </div> : <NavLink to={"/login"}>
              <div className={classes.btn}>
                <Button size="small" color="secondary">
                  Login
                </Button>
              </div>
            </NavLink>}
        </div>
      </header>;
}

export default Header;